.contGoogle {
    margin: 0 15px;
    display: flex;
    padding: 11px 19px;
    justify-content: center;
    align-items: center;
    font-family: var(--font-inter);
    font-weight: 600;
    font-size: 14px;
    gap: 10px;
    border-radius: 50px;
    border: 1px solid var(--border-color);
    text-decoration: none;
    color: var(--main-text-color);

    .googleImg {
        font-size: 0;

        img {
            width: 20px;
        }
    }
}

.choiceMethod {
    margin: 20px 15px 28px;
    position: relative;
    hr {
        background-color: var(--border-color);
        opacity: 0.2;
    }
    span {
        display: flex;
        position: absolute;
        top: 50%;
        background-color: white;
        padding: 0 20px;
        left: 50%;
        transform: translate(-50%, -50%);
        color: var(--grey);
        text-align: center;
        font-family: Inter;
        font-size: 12px;
        font-style: normal;
        font-weight: 600;
        line-height: normal;
    }
}

.enterEmailPass {
    margin-bottom: 14px;
    input {
        display: flex;
        width: 365px;
        margin: 0 auto;
        padding: 13px 19px;
        align-items: center;
        gap: 10px;
        border-radius: 50px;
        border: 1px solid var(--border-color);
        background: #ffffff;
        font-size: 14px;
        letter-spacing: 0px;
        box-shadow: 2px 2px 5px var(--border-color);
    }
    input::placeholder {
        color: var(--grey);
        font-family: var(--font-inter);
        font-style: normal;
        font-weight: 400;
        line-height: normal;
    }
}

.defaultBtn {
    display: inline-flex;
    width: 377px;
    padding: 11px 19px;
    justify-content: center;
    align-items: center;
    gap: 10px;
    border-radius: 50px;
    background: var(--main-text-color);
    cursor: pointer;
    position: relative;
    margin: 4px 15px 30px;
    color: var(--white);
    text-align: center;
    font-family: Inter;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;

    &:hover {
        background: var(--grey-darker);
    }

    &:disabled {
        cursor: initial;
        background: var(--grey);

        &:hover {
            background: var(--grey);
        }
    }
}

.disableBtn {
    animation: disableBtn 0.2s infinite linear;
}

@keyframes disableBtn {
    25% {
        left: 5px;
    }
    50% {
        left: 0;
        right: 5px;
    }
    75% {
        right: 0;
        left: 5px;
    }
    100% {
        left: 0;
    }
}

@import './MediaShared.scss';
