@media screen and (max-width: 768px) {
    .createPassword {
        .modal-wrapper {
            padding: 0 15px;
            width: 100%;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            .modalTitle {
                display: block;
                padding: 0vh 0 5vh;
                position: relative;
                bottom: 4px;
            }
            .passwordWrapper {
                flex-direction: column;
                margin: 0;
                position: relative;
                bottom: 9px;
                width: 100%;
                .password {
                    .passwordTitle {
                        display: none;
                    }

                    .enterPassword {
                        // padding-bottom: 1.5vh;
                        // min-height: 8.5vh;
                        margin-bottom: 15px;
                        input {
                            // width: clamp(290px, 80vw, 650px);
                            width: -webkit-fill-available;
                        }
                    }
                }
            }
            .defaultBtn {
                position: relative;
                top: 3px;
            }
        }
    }
}
