.editProfile {
    width: -webkit-fill-available;
    height: -webkit-fill-available;

    .container {
        .editPageWrapper {
            padding: 40px 0;
            .closeBtn {
                display: none;
            }
            .backBtn {
                text-align: left;
                padding: 11px 19px;
                span {
                    padding-left: 10px;
                    color: var(--grey);
                    font-family: var(--font-inter);
                    font-size: 14px;
                    font-style: normal;
                    font-weight: 500;
                    line-height: normal;
                }
            }
            .modalTitle {
                h2 {
                    color: var(--main-text-color);
                    font-family: var(--font-inter);
                    font-size: 58.148px;
                    font-weight: 500;
                    line-height: 46.176px;
                    text-transform: uppercase;
                    margin-top: 24px;
                    margin-bottom: 70px;
                }
            }
            .formWrapper {
                display: grid;
                grid-template-columns: 55% 35%;
                grid-auto-rows: auto;
                gap: 63px;
                position: relative;

                .rowWrapper {
                    display: grid;

                    grid-auto-rows: auto;
                    gap: 10px;

                    .userAvatar {
                        position: relative;
                        img {
                            width: 100%;
                            height: 100%;
                        }
                        .avatarBtn {
                            position: absolute;
                            bottom: 0;
                            left: 0;
                            padding: 8px 0 10px;
                            cursor: pointer;
                            background-color: rgba(0, 0, 0, 0.4);
                            border-radius: 0 0 5px 5px;
                            width: 100%;
                            img {
                                width: auto;
                            }
                            span {
                                color: var(--white);
                                font-family: var(--font-inter);
                                font-size: 14px;
                                font-style: normal;
                                font-weight: 500;
                                line-height: normal;
                                padding-left: 10px;
                            }
                        }
                    }

                    .inputItem {
                        height: fit-content;
                        .inputWrapper {
                            text-align: start;
                            padding: 0 10px;
                            .titleWrapper {
                                position: relative;
                                .hoverBtn {
                                    width: 17px;
                                    height: 17px;
                                    background: #ebebed;
                                    border-radius: 50%;
                                    display: inline-block;
                                    margin-left: 5px;
                                    position: absolute;
                                    text-align: center;
                                    cursor: pointer;
                                    bottom: 10px;
                                }
                            }
                            h4 {
                                color: var(--main-text-color);
                                font-family: var(--font-inter);
                                margin-bottom: 10px;
                                font-size: 14px;
                                font-style: normal;
                                font-weight: 600;
                                line-height: normal;
                                width: fit-content;
                                display: inline-block;
                            }

                            input {
                                padding: 13px 19px;
                                border-radius: 50px;
                                width: -webkit-fill-available;
                                border: 1px solid var(--Button-Stroke, #ebebed);
                                background: #ffffff;
                            }
                        }
                    }
                    .userDescription {
                        grid-column-start: 1;
                        grid-column-end: 4;
                        padding-bottom: 40px;
                        border-bottom: 1px solid var(--border-color);
                        .inputWrapper {
                            text-align: start;
                            padding: 0 10px;
                            .titleWrapper {
                                position: relative;
                                .hoverBtn {
                                    width: 17px;
                                    height: 17px;
                                    background: var(--thumb-background-color);
                                    border-radius: 50%;
                                    display: inline-block;
                                    margin-left: 5px;
                                    position: absolute;
                                    text-align: center;
                                    cursor: pointer;
                                    bottom: 10px;
                                }
                            }
                            h4 {
                                color: var(--main-text-color);
                                font-family: var(--font-inter);
                                margin-bottom: 10px;
                                font-size: 14px;
                                font-style: normal;
                                font-weight: 600;
                                line-height: normal;
                                width: fit-content;
                                display: inline-block;
                            }
                            textarea {
                                padding: 13px 19px;
                                border-radius: 15px;
                                height: 133px;
                                width: -webkit-fill-available;
                                border: 1px solid var(--Button-Stroke, #ebebed);
                                background: var(--white);
                            }
                        }
                    }
                    .passwordWrapper {
                        display: flex;
                        grid-column-start: 1;
                        grid-column-end: 4;
                        align-items: flex-end;
                        position: relative;
                        .inputWrapper {
                            text-align: start;
                            padding: 0 10px;
                            h4 {
                                color: var(--main-text-color);
                                font-family: var(--font-inter);
                                margin-bottom: 10px;
                                font-size: 14px;
                                font-style: normal;
                                font-weight: 600;
                                line-height: normal;
                                width: fit-content;
                                display: inline-block;
                            }
                            input {
                                padding: 13px 19px;
                                border-radius: 50px;
                                width: -webkit-fill-available;
                                border: 1px solid var(--Button-Stroke, #ebebed);
                                background: #ffffff;
                            }
                        }
                        .defaultBtn {
                            margin: 0;
                            width: 144px;
                            height: 45px;
                            background-color: transparent;
                            color: var(--grey);
                            padding: 14px 11px;
                        }
                    }
                }
                .leftWrapper {
                    grid-template-columns: 26% 1fr 1fr;
                }
                .rightWrapper {
                    grid-template-columns: 50% 50%;
                    grid-auto-rows: max-content;
                    .separateLine {
                        height: fit-content;
                        grid-column-start: 1;
                        grid-column-end: 3;
                        margin: 40px 0 21px;
                        hr {
                            border-width: 1px;
                            border-color: var(--border-color);
                        }
                    }
                    .buttonWrapper {
                        display: flex;
                        justify-content: flex-end;
                        width: -webkit-fill-available;
                        grid-column-start: 1;
                        grid-column-end: 3;
                        position: absolute;
                        bottom: 0;
                        .btn {
                            margin-right: 20px;
                        }
                        .cancelBtn {
                            .defaultBtn {
                                width: 144px;
                                height: 45px;
                                background-color: transparent;
                                color: var(--grey);
                                margin: 0;
                            }
                        }
                        .saveBtn {
                            .defaultBtn {
                                width: 144px;
                                height: 45px;
                                padding: 11px 19px;
                                margin: 0;
                            }
                        }
                    }
                }
            }
        }
    }
}

@import './MediaEditProfile.scss';
