@import url('https://fonts.googleapis.com/css2?family=Caveat&family=Inter:wght@400;500;600;700&display=swap');

/* FONTS START */
:root {
    --font-caveat: 'Caveat';
    --font-inter: 'Inter';
    --font-family-caveat: var(--font-caveat), cursive;
    --font-family-inter: var(--font-inter), sans-serif;

    // COLORS
    --main-text-color: #141414;

    --border-color: #ebebed;
    --thumb-border-color: #f8f7f4;

    --thumb-background-color: #cdcdcd;
    --footer-background-color: #f6f6f6;

    --grey-lighter: #bebebe;
    --grey: #9e9e9e;
    --grey-darker: #595959;

    --green: #05cd55;

    --red: #ff5050;

    --white: #ffffff;
    --black: #000000;

    // VARIABLES FROM DESIGN
    --Main-dark: var(--main-text-color);
    --Grey: var(--grey);
    /* FONTS END */
}

html {
    height: 100%;
    min-width: 320px;
    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
}

body {
    font: 14px/1.2 var(--font-family-inter);
    height: 100%;
    min-width: 320px;
    position: relative;
}

.App {
    text-align: center;
    min-height: 100vh;
}

.wrapper {
    display: table;
    width: 100%;
    height: 100vh;
    table-layout: fixed;
    overflow: hidden;
}

.container {
    // max-width: 1180px;
    max-width: clamp(240px, 1180px, 2000px);
    width: calc(100% - 30px);
    margin: 0 auto;
    padding: 0 15px;
}

footer {
    height: 1px;
    display: table-row;
}

.vLine {
    display: inline-block;
    width: 1px;
    height: 20px;
    background-color: var(--Grey, #9e9e9e);
    margin: 0 10px;
}
