@media screen and (max-width: 768px) {
    .modal.supportModal {
        .supportWrapper {
            .modalTitle {
                padding: 29px 0 29px;
            }

            .inputWrapper {
                width: 100% !important;
                input {
                    width: -webkit-fill-available;
                }
            }

            .describeRequest {
                #description {
                    height: 70px;
                    border-radius: 15px;
                    margin-bottom: 30px;
                }
            }
        }
    }
}
